import React, { useRef, useState, useEffect } from "react";
import ReactPlayer from "react-player";

function getRandomInt(min, max) {
  min = Math.ceil(min);
  max = Math.floor(max);
  return Math.floor(Math.random() * (max - min) + min); // The maximum is exclusive and the minimum is inclusive
}

 function App({handleReset}) {
  const [isPlaying, setIsPlaying] = useState(true);
    const [progress, setProgress] = useState(0);
  const [isReady, setIsReady] = React.useState(false);
      const videoRef = useRef(null);

const onReady = React.useCallback(() => {
  
  if (!isReady) {
    let timenow = localStorage.getItem('timenow')
    // const timeToStart = (7 * 60) + 12.6;
    videoRef.current.seekTo(timenow, "seconds");
    setIsReady(true);
  }
}, [isReady]);

  const resetMatch = () => {
    videoRef.current.seekTo(0, "seconds");
    handleReset()
  }


    const togglePlay = () => {
//         if (isPlaying) {
//             videoRef.current.pause();
//         } else {
//             videoRef.current.play();
//         }
        setIsPlaying(!isPlaying);
    };

    const handleProgress = (e) => {
        const duration = videoRef.current.getDuration() ;
        const currentTime = videoRef.current.getCurrentTime();
  const progress = (currentTime / duration) * 100;
        setProgress(progress);
        localStorage.setItem('timenow', currentTime)
    };





    return (
        <div>
            <ReactPlayer
                ref={videoRef}
                autoPlay
                muted
                onProgress={handleProgress}
//                 onTimeUpdate={handleProgress}
                width="100%"
                height="100%"
//                 controls
                playing={isPlaying}
                url="https://content-file-upload.s3.amazonaws.com/sabong.mp4"
                playsInline
                onReady={() => onReady()}
            />
{/*             <div>
                <button onClick={() => resetMatch()}>
                    Reset
                </button>
                <button onClick={togglePlay}>
                    Play
                </button>
                <progress value={progress} max="100" />
            </div> */}
        </div>
    )
}


export default App